import React from 'react'
import PropTypes from 'prop-types'
import styles from './profile.module.css'

const Stats = ({followers, viesws, likes}) => {
    return (
    <ul className={styles.statsList}>
        <li className={styles.statsItem}>
            <span >Followers</span>
            <span className={styles.statsValue}>{followers}</span>
        </li>
        <li className={styles.statsItem}>
            <span>Views</span>
            <span className={styles.statsValue}>{viesws}</span>
        </li>
        <li className={styles.statsItem}>
            <span>Likes</span>
            <span className={styles.statsValue}>{likes}</span>
        </li>
    </ul>
    )
}

Stats.propTypes = {
    followers: PropTypes.number.isRequired,
    viesws: PropTypes.number.isRequired,
    likes: PropTypes.number.isRequired,
}

export default Stats